<template>
    <div id="nav">
      <nav>
        <span :style="[small? {float: 'right', paddingRight: '70%'} : {}]">Joash-JW</span>
        <button :style="[small? {display: 'block'}:{display: 'none'}]" v-on:click="toggle"><img src="https://img.icons8.com/ios/24/000000/menu.png"></button>
        <ul v-bind:style="[small? ulSmall : {}]">
          <li v-bind:style="[small? liSmall : liLarge]"><router-link to="/">Home</router-link></li>
          <li v-bind:style="[small? liSmall : liLarge]"><router-link to="/experience">Experience</router-link></li>
          <li v-bind:style="[small? liSmall : liLarge]"><router-link to="/projects">Projects</router-link></li>
        </ul>
      </nav>
    </div>
</template>

<script>
export default {
  name: 'NavBar',
  data () {
    return {
      small: false,
      liSmall: {
        display: 'none',
        margin: 0
      },
      liLarge: {
        display: 'block'
      },
      ulSmall: {
        'flex-direction': 'column',
        float: 'left'
      }
    }
  },
  methods: {
    toggle () {
      var x = document.getElementsByTagName('li')
      var i
      for (i = 0; i < x.length; i++) {
        if (x[i].style.display === 'none') x[i].style.display = 'block'
        else x[i].style.display = 'none'
      }
    }
  },
  created () {
    if (window.innerWidth <= 700) {
      this.small = true
    }
  },
  mounted () {
    window.onresize = () => {
      if (window.innerWidth <= 700) {
        this.small = true
      } else this.small = false
    }
  }
}
</script>

<style scoped>
nav {
  width: 100vw;
  font-size: 3.2vh;
  padding-top: 2vh;
  background-color: white;
  padding-bottom: 2vh;
  text-transform: uppercase;
  position: fixed;
}
li {
  list-style-type: none;
  padding: 0 1vw 0 1vw;
  display: block;
}
a {
  text-decoration: none;
  font-family: 'Varela', sans-serif;
  color: grey;
  padding: 0.5vh 1vw 0.5vh 1vw;
}
ul {
  padding: 0;
  margin: 0;
  display: flex;
  float: right;
  padding-right: 2vw;
}
span {
  float: left;
  padding-left: 2vw;
  font-weight: bold;
  font-family: 'Open Sans', sans-serif;
}
nav a.router-link-exact-active {
  background-color: #e8e8e8;
}
button {
  display: none;
  cursor: pointer;
  border: none;
  outline: none;
  background-color: white;
}
button:hover {
  background-color: #e8e8e8;
}
</style>
