import Vue from 'vue'
import Router from 'vue-router'
import HomePage from './views/HomePage.vue'
import ExperiencePage from './views/ExperiencePage.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'homepage',
      component: HomePage
    },
    {
      path: '/experience',
      name: 'experiencepage',
      component: ExperiencePage
    }
  ]
})
